body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.views-home,
.views-subtitle,
.views-shadowing {
  padding: 20px;
}
.views-home .home-nav a {
  display: block;
  margin: 10px;
  padding: 10px;
}
/* subtitle */
.views-subtitle {
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.views-subtitle h2 {
  flex: none;
  margin: 0 0 5px;
}
.views-subtitle .player {
  flex: none;
}
.views-subtitle .subtitles {
  flex: auto;
  overflow: auto;
}
.views-subtitle .subtitles .subtitle {
  margin: 10px 0;
  padding-left: 30px;
  position: relative;
}
.views-subtitle .subtitles .subtitle.current {
  color: red;
}
.views-subtitle .subtitles .content {
  cursor: pointer;
}
.views-subtitle .subtitles .merge-up {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  padding: 3px;
  cursor: pointer;
  visibility: hidden;
}
.views-subtitle .subtitles .subtitle:hover .merge-up {
  visibility: visible;
}
.views-subtitle .btns {
  flex: none;
  margin-top: 10px;
}
.views-subtitle .btns .btn {
  padding: 10px 40px;
  font-size: 20px;
  border-radius: 5px;
}
.views-subtitle .btns .btn:focus {
  outline: none;
}

/* shadowing */
.views-shadowing {
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.views-shadowing h2 {
  flex: none;
  margin: 0 0 5px;
  display: flex;
  justify-content: space-between;
}
.views-shadowing .settings .settings-field {
  display: flex;
  align-items: center;
  padding: 4px 0;
}
.views-shadowing .settings .settings-field span {
  width: 100px;
}
.views-shadowing .settings .settings-field select {
  font-size: 16px;
  width: 100px;
}
.views-shadowing .player {
  flex: none;
  display: none;
}
.views-shadowing .countdown-bar {
  flex: none;
  height: 2px;
  background-color: green;
  transition: width 3s linear;
  width: 100%;
  visibility: hidden;
}
.views-shadowing .subtitles {
  flex: auto;
  overflow: auto;
}
.views-shadowing .subtitles .subtitle {
  margin: 20px 0;
  position: relative;
  cursor: pointer;
  font-size: 18px;
  line-height: 24px;
  transition: color 0.3s;
}
.views-shadowing .subtitles .subtitle.current {
  color: red;
}
.views-shadowing .btns {
  flex: none;
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
}
.views-shadowing .btns .btn {
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  height: 45px;
  box-sizing: border-box;
  border: 1px solid #999;
  display: flex;
  align-items: center;
}
.views-shadowing .btns .btn:focus {
  outline: none;
}
